/* Body of page css */
#body-css {
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(132,179,233,1) 100%);
}

/* Nav bar */

.homenav { 
    display: flex;
    justify-content: left;
    list-style-type: none;
    margin: auto;
    font-size: 1.1em;
    font-family: 'Orbitron', sans-serif;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(30,111,184,0.8449754901960784) 100%);
    color: whitesmoke;
}

li:hover {
    color: rgba(206, 206, 206, 0.86);
}

  /* Company name in nav bar */

  #nav-company-name {
    font-family: 'Notable', sans-serif;
    font-size: 1.3em;
    border-right: whitesmoke solid 2px;
  }

.homenav, li {
    padding-top: .2em;
    padding-left: .5em;
    padding-right: .5em;
    border-left: black solid 2px;
}

/* New Movie Trailer */

.hometrailer {
    width: 40em;
    height: 25em;
    margin: auto;
    padding: 0px 0px;
}

/* New Movies */

.new-movie-genre-title {
    margin-top: 1.2em;
    margin-bottom: .2em;
    font-size: 2em;
    font-family: 'Orbitron', sans-serif;
    border-top: black solid 2px;
}

.new-release-container {
    display: flex;
    padding: 0px 0px;
    margin: 0px 0px;
    height: 16em;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
}

.new-release-movie-poster-box {
    display: inline-block;
    height: 16em;
    padding: 0px 0px;
    margin: 0px 0px;
    
}

.new-release-movie-poster-box:hover {
    color: rgba(37, 41, 237, 0.75);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.new-release-movie {
    display: flex;
    height: 13em;
    width: 20em;
    padding: 0px 0px;
    margin: 0px 2px;
    border: black solid 2px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.new-release-title {
    display: flex;
    height: 2.4em;
    width: 20em;
    padding: 0px 0px;
    margin: 0px 2px;
    justify-content: center;
    background-color: rgba(229, 229, 229, 0.75);
    border: black solid 2px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: 'Orbitron', sans-serif;
    overflow: scroll;
}

